import { playsound } from './sound.js'
import { drawImage } from './util.js'

class Van {
    constructor(x, y, config) {
        this.x = x
        this.y = y
        this.c = config

        this.ismoving = true

        this.rot = 0
        this.bob = 0
        this.inc = 1
        this.count = 0
    }

    render(c) {
        // draw the van
        c.drawImage(this.c[19], this.x, this.y - this.bob)
        // and the wheels
        // front
        drawImage(c, this.c[20], this.x + 131, this.y + 78, 44, 44, this.rot)
        // back
        drawImage(c, this.c[20], this.x + 23, this.y + 78, 44, 44, this.rot)
    }

    update() {
        // bob up and down
        
        if(this.count > 3) {
            this.count = 0
            this.bob += this.inc
            if(this.bob > 1) {
                this.inc = -1
            }
            if(this.bob < 0) {
                this.inc = 1
            }
        }
        this.count++

        // wheels rotate if we're moving
        if(this.ismoving) {
            this.rot -= 2
            if(this.rot < -360) {
                this.rot = 0
            }
        }
    }

    beep() {
        playsound(1)
    }

}

export default Van